import React, { PureComponent } from 'react'
import { ResumeWrapper } from './style'

class Resume extends PureComponent {
  render() {
    return (
      <ResumeWrapper>
        <div className='main'>
          Resume
          <i className='iconfont'>
          &#xe6bc;</i>
        </div>
      </ResumeWrapper>
    );
  }
}

export default Resume